import React, { useEffect, useState, useContext } from 'react';
import CkEditor from "components/misc/CkEditor";
import {Container, ButtonStyle2} from "components/misc/CustomTheme";
import { useHistory } from "react-router-dom"
import UserContext from "global/UserContext"
import { useParams } from "react-router-dom"
import axios from "global/axiosbase"
import ContentParser from "components/misc/ContentParser"

const BusinessInfoOne = ({ modal }) => {
    const history = useHistory();
    const ctx = useContext(UserContext);
    const param = useParams().id;
    const [ fetchID, setFetchID ] = useState(null);
    const [ data, setData ] = useState('');
    const [ errTxt, setErrTxt ] = useState(false);

    useEffect(()=>{
        fetchData();
    },[]);

    const fetchData = () =>{
        axios.get(`businessinfoones?idd=${param}`, ).then(res=>{
            if(res.data.length){
                setData(res.data[0]);
                setFetchID(res.data[0]?.id);
            }
        })
    }
   
    const clickHandle = () =>{
        if(data.length){
            ctx.loadFunc(true);
            if(fetchID){
                axios.put(`businessinfoones/${fetchID}`, { body: data, idd: param }).then(res=>{
                    ctx.alertFunc('green','',true );
                    ctx.loadFunc(false);
                    history.push(`/${param}/businessinfo/2`);
                }).catch(err=>ctx.alertFunc('orange',' ',true ));
            }else{
                axios.post(`businessinfoones`, { body: data, idd: param }).then(res=>{
                    axios.put(`totals/${ctx.total?.id}`, { businessone: true, idd: param }).then(res=>{
                        ctx.alertFunc('green','',true );
                        ctx.loadFunc(false);
                        history.push(`/${param}/businessinfo/2`);
                    }).catch(err=>ctx.alertFunc('orange',' ',true ));
                }).catch(err=>ctx.alertFunc('orange',' ',true ));
            }
        }else{
            setErrTxt(true);
        }
    }

    return (
        <>
        {modal?<ContentParser data={data?.body} titleSm={``} titleBig={`III.      `} />
        :<Container>
            <CkEditor data={data?.body} setData={setData} height={20} title={``} />


            <ButtonStyle2 >
                <div className="errTxt">{errTxt&&`  `}</div>
                <button onClick={clickHandle}  className="myBtn"></button>
            </ButtonStyle2>
        </Container>}
            
        </>
    )
}

export default BusinessInfoOne
